@charset "utf-8";

/*!
 * @base.css
 * @1
 *       2
 */
/*  */
html {
    background: white;
    color: black;
    font-size: 10px;
    // filter: grayscale(0.95);
    height: 100%;
}

#root,
body {
    height: 100%;
}

/*  */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    margin: 0;
    padding: 0;
}

/*  font  */
body,
button,
input,
select,
textarea {
    font: 14px \5b8b\4f53, arial, sans-serif;
}

input,
select,
textarea {
    font-size: 100%;
}

/*  table cell  */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

/* ie bugth  text-align */
th {
    text-align: inherit;
}

/*  */
fieldset,
img {
    border: none;
}

/* ie6 7 8(q) bug  */
iframe {
    display: block;
}

/*  firefox  */
abbr,
acronym {
    border: none;
    font-variant: normal;
}

/*  del  */
del {
    text-decoration: line-through;
}

address,
caption,
cite,
code,
dfn,
em,
th,
var {
    font-style: normal;
    font-weight: 500;
}

/* li  */
ol,
ul {
    list-style: none;
}

/*  */
caption,
th {
    text-align: left;
}

/* yahoo */
h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 100%;
    font-weight: 500;
}

h1 {
    font-size: 2.6rem;
}

h2 {
    font-size: 2.4rem;
}

h3 {
    font-size: 2rem;
}

q:before,
q:after {
    content: '';
}

/*  */
sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sup {
    top: -0.5em;
}

sub {
    bottom: -0.25em;
}

// /*  hover  */
// a:hover {
//     text-decoration: underline;
// }

/*  */
ins,
a {
    text-decoration: none;
}

/*  ie6 & ie7  */
a:focus,
*:focus {
    outline: none;
}

/*  */
.clearfix:before,
.clearfix:after {
    content: '';
    display: block;
}

.clearfix:after {
    clear: both;
    overflow: hidden;
}

.clearfix {
    zoom: 1;
    /* for ie6 & ie7 */
}

.clear {
    clear: both;
    display: block;
    font-size: 0;
    height: 0;
    line-height: 0;
    overflow: hidden;
}

/*  js  */
.hide {
    display: none;
}

.block {
    display: block;
}

/*  bug */
.fl,
.fr {
    display: inline;
}

.fl {
    float: left;
}

.fr {
    float: right;
}

.mr15 {
    margin-right: 15px;
}